@import "https://fonts.googleapis.com/css2?family=Montserrat:wght@200;400&display=swap";
.btn-chart {
  color: #0000;
  font-size: 1.5em;
  transition: all .5s;
}

.btn-chart:hover {
  scale: 1.1;
  color: #00f !important;
}

.card-container {
  justify-content: center;
  align-items: center;
  display: flex;
}

.card {
  background: inherit;
  color: #fff;
  height: 100%;
  border: none;
  transition: all .7s;
}

.card:hover {
  opacity: 1;
  color: #000;
  background-color: #fff;
  border: none;
  scale: 1.005;
}

.card:hover .card-img-top {
  filter: none;
  animation: 2s infinite unfocus;
}

.card:hover .btn-chart {
  color: red;
  animation: 2s infinite bigsmall;
}

.card-body {
  flex-direction: column;
  display: flex;
}

#settingsButton {
  color: #000;
  cursor: pointer;
  background: #69bdf5;
  border: 3px solid #303030;
  border-radius: 100%;
  padding: 15px;
  position: fixed;
  bottom: 30px;
  right: 30px;
}

#settingsButton:hover {
  color: #fff;
  background: #474747;
}

#timer {
  color: green;
  border: 3px solid green;
  border-radius: 100%;
  padding: 15px;
  font-weight: 800;
  position: fixed;
  bottom: 100px;
  right: 30px;
}

.stock-price {
  font-family: Montserrat, sans-serif;
  font-size: 2em;
}

.configLogo {
  cursor: pointer;
  margin: 5px;
}

#targetImagenes {
  height: 300px;
  -ms-overflow-style: none;
  scrollbar-width: none;
  margin: 1em;
  overflow-x: hidden;
  overflow-y: scroll;
}

#targetImagenes::-webkit-scrollbar {
  display: none;
}

.selectBox {
  margin-top: 1em;
  margin-bottom: 1em;
  padding: 1em;
}

#unselectedStocks {
  background-color: #b45d5d80;
  border-right: 1px solid #000;
}

#selectedStocks {
  background-color: #64b45d80;
  border-left: 1px solid #000;
}

.navbar {
  font-family: Montserrat, sans-serif;
  font-size: 1.2em;
  text-decoration: none;
}

.navbar a {
  color: inherit;
}

body {
  background: linear-gradient(-45deg, #141414, #000, #222f33, #041d17) 0 0 / 400% 400%;
  animation: 8s infinite gradient;
}

@keyframes gradient {
  0% {
    background-position: 0%;
  }

  50% {
    background-position: 100%;
  }

  100% {
    background-position: 0%;
  }
}

@keyframes unfocus {
  0% {
    filter: none;
  }

  25% {
    filter: blur(1px);
  }

  50% {
    filter: blur(2px);
  }

  75% {
    filter: blur(1px);
  }

  100% {
    filter: none;
  }
}

@keyframes bigsmall {
  0% {
    scale: 1;
  }

  50% {
    scale: 1.1;
  }

  100% {
    scale: 1;
  }
}

